export default {
	data() {
		return {
			id:0,
			newsDetail: {}
		}
	},
	methods: {
		async getNewsDetail(id){
		    const res = await this.$myRequest({
		      url: '/api/getnew/' + this.id
		    })
		    this.newsDetail = res.data.message[0]
		}
	},
	 onLoad (options){
		this.id = options.id
	    this.getNewsDetail()
	}
}
